import { useEffect, useState } from 'react';
import { Link as RouterLink, useParams } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import {
  Box,
  Breadcrumbs,
  Container,
  Divider,
  Grid,
  Link,
  // Tab,
  // Tabs,
  Typography
} from '@material-ui/core';
import AccountGeneralSettings from '../../../components/dashboard/driver/AccountGeneralSettings';
import useSettings from '../../../hooks/useSettings';
import ChevronRightIcon from '../../../icons/ChevronRight';
import gtm from '../../../lib/gtm';
// import useAuth from '../../../hooks/useAuth';
import DriversRepository from '../../../repositories/drivers.repository';

const DriverDetail = () => {
  const { settings } = useSettings();
  const driversRepository = new DriversRepository();
  const [client, setClient] = useState({});
  // const client = [];
  const { id } = useParams();
  //   const { user } = useAuth();
  // const navigate = useNavigate();

  useEffect(() => {
    driversRepository.getUserById(id).then((res) => {
      setClient(res.data);
    })
      .catch((error) => {
        console.log(error);
        // navigate('/dasboard/drivers');
      });
    gtm.push({ event: 'page_view' });
  }, []);

  if (!(Object.keys(client).length > 0)) {
    return null;
  }

  return (
    <>
      <Helmet>
        <title>Dashboard: Detalles del Driver | Mac</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 8
        }}
      >
        <Container maxWidth={settings.compact ? 'xl' : false}>
          <Grid
            container
            justifyContent="space-between"
            spacing={3}
          >
            <Grid item>
              <Typography
                color="textPrimary"
                variant="h5"
              >
                Detalle del Driver
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard/drivers"
                  variant="subtitle2"
                >
                  Listado de Drivers
                </Link>
                <Typography
                  color="textSecondary"
                  variant="subtitle2"
                >
                  Detalle del Driver
                </Typography>
              </Breadcrumbs>
            </Grid>
          </Grid>
          <Divider />
          <Box sx={{ mt: 3 }}>
            <AccountGeneralSettings
              driver={client}
            />
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default DriverDetail;
